import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import {HomePage} from '../pages/home/home';
import { MyApp } from './app.component';
import { LoginPage } from '../pages/login/login';
import { MemloginPage } from '../pages/memlogin/memlogin';
import { ToolboxHomePage } from '../pages/toolbox-home/toolbox-home';
import {ModalpagePage} from '../pages/modalpage/modalpage';
import{ DBService } from '../services/database.service';
import{ HttpClientModule } from '@angular/common/http';
import { ToolboxDescriptionPage } from '../pages/toolbox-description/toolbox-description';
import { AddtoolPage } from '../pages/add-tool/add-tool';
import {EditToolBox} from '../pages/toolbox-edit/toolbox-edi'
import { EditToolPage } from '../pages/edit-tool/edit-tool';
import { ScanModalPage } from '../pages/scan-modal/scan-modal';
import { ToolboxScanPage } from '../pages/toolbox-scan/toolbox-scan';
import { AircraftScanPage } from '../pages/aircraft-scan/aircraft-scan';
// import { HeaderPage } from '../pages/header/header';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LoginPage,
    MemloginPage,
    ToolboxHomePage,
    ModalpagePage,
    ToolboxDescriptionPage,
    AddtoolPage,
    EditToolBox,
    EditToolPage,
    ScanModalPage,
    ToolboxScanPage,
    AircraftScanPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    MemloginPage,
    ToolboxHomePage,
    ModalpagePage,
    ToolboxDescriptionPage,
    AddtoolPage,
    EditToolBox,
    EditToolPage,
    ScanModalPage,
    ToolboxScanPage,
    AircraftScanPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    DBService,
    HttpClientModule
  ]
})
export class AppModule {}



