import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { DBService } from '../../services/database.service';

/**
 * Generated class for the AircraftScanPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-aircraft-scan',
  templateUrl: 'aircraft-scan.html',
})
export class AircraftScanPage {
  scanStatus;
  count = 0;
  myTool = [];
  rfid = ['To1', 'to3', 'To4'];
  constructor(public navCtrl: NavController, public navParams: NavParams, public db:DBService) {
    // this.db.getAllTools();
    this.viewTool();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AircraftScanPage');
  }

  
  viewTool() {
    this.scanStatus = 'Scanning';
    this.db.getAllTools().subscribe((response) => {
      this.myTool = [];
      console.log(response);
      for(let i in response) {
        if(this.rfid.includes(response[i]['tool_id'])) {
          this.myTool.push({
            toolImage: response[i]['tool_image'],
            toolName: response[i]['tool_name'],
            toolDescription: response[i]['tool_desc'],
            toolId: response[i]['tool_id']
          });
          this.count++;
        }
      }
    });
    this.scanStatus = 'Scan Completed';
   }

}



