import { Component } from '@angular/core';
import { NavController, NavParams, PopoverController, ViewController } from 'ionic-angular';
import { ModalpagePage} from '../modalpage/modalpage';
import { DBService } from '../../services/database.service';
import {ToolboxDescriptionPage}from '../../pages/toolbox-description/toolbox-description';
import { HeaderPage } from '../header/header';
/**
 * Generated class for the ToolboxHomePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-toolbox-home',
  templateUrl: 'toolbox-home.html',
})
export class ToolboxHomePage {

  myToolBox = [];

  constructor(public navCtrl: NavController, public navParams: NavParams,public popoverCtrl:PopoverController, public db: DBService, public viewCtrl: ViewController) {
    this.viewToolBox();
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ToolboxHomePage');
    // this.viewCtrl.showBackButton(false);
  }
  // images=[
  //   {'url':"../assets/imgs/download (1).jfif",
  //   'title':" Metal Step Cases"},
  //   {'url':"../../assets/imgs/48228426.png",
  //   'title':"Trolley cases"},
  //   {'url':"../../assets/imgs/R8782810-01.jpg",
  //   'title':"RS Components"},
  //   {'url':"../../assets/imgs/98220_150dpi_midres_1_2.jpg",
  //   'title':"Aviation Mantinance Kits"},
  //   {'url':"../../assets/imgs/s-l300.jpg",
  //   'title':"WorkShow CabinateKit"},
  //   {'url':"../../assets/imgs/48228426.png",
  //   'title':"Aircraft maintinance kit"},
  //   {'url':"../../assets/imgs/98220_150dpi_midres_1_2.jpg",
  //   'title':"workshow kit"}
  //  ]



   openmodel(myEvent){
    let popover = this.popoverCtrl.create(ModalpagePage);
    popover.present({
       ev:myEvent
    });
    popover.onDidDismiss(() => {
      this.viewToolBox();
    }); 
   }
   viewToolBox(){
    this.db.getToolBox().subscribe((response) => {
      this.myToolBox = [];
      console.log(response);
      for(let i in response) {
      this.myToolBox.push({
        toolboxImage: response[i]['tb_image'],
        toolboxName: response[i]['tb_name'],
        toolboxId: response[i]['tb_id']
      });
      }
    });
   }
   
   nextPage(toolboxId){
     this.navCtrl.push(ToolboxDescriptionPage, {
       toolboxId: toolboxId
     });
   }
}



