import { Component } from '@angular/core';
import { NavController, NavParams, PopoverController, ViewController } from 'ionic-angular';
import{ AddtoolPage } from '../add-tool/add-tool';
import { EditToolBox } from '../toolbox-edit/toolbox-edi';
import { DBService } from '../../services/database.service';
import { ToolboxHomePage } from '../toolbox-home/toolbox-home';
import { EditToolPage } from '../edit-tool/edit-tool';
import { ScanModalPage } from '../scan-modal/scan-modal';

/**
 * Generated class for the ToolboxDescriptionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-toolbox-description',
  templateUrl: 'toolbox-description.html',
})
export class ToolboxDescriptionPage {

  toolboxName;
  toolboxId;
  toolboxType;
  toolboxDescription;
  toolboxImage;
  myTool = [];

  constructor(public navCtrl: NavController, public viewCtrl: ViewController, public navParams: NavParams, public popoverCtrl:PopoverController, public db: DBService) {
    this.toolboxId = navParams.get('toolboxId');
    this.viewToolBox();
    this.viewTool();
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ToolboxDescriptionPage');
    this.viewCtrl.showBackButton(false);
  }

  openmodel(myEvent){
    let popover = this.popoverCtrl.create(AddtoolPage, {
      toolboxId: this.toolboxId
    });
    popover.present({
       ev:myEvent
    });
    popover.onDidDismiss(() => {
      this.viewTool();
    }); 
  }
  editmodel(myEvent){
    let popover = this.popoverCtrl.create(EditToolBox, {
      toolboxId: this.toolboxId
    });
    popover.present({
       ev:myEvent
    });
    popover.onDidDismiss(() => {
      this.viewToolBox();
    }); 
   }
   gotoHome() {
     this.navCtrl.setRoot(ToolboxHomePage);
   }
   viewToolBox() {
    this.db.getToolBoxById({'toolboxId':this.toolboxId}).subscribe((response) => {
      this.toolboxName = response[0]['tb_name'];
      this.toolboxId = response[0]['tb_id'];
      this.toolboxType = response[0]['tb_type'];
      this.toolboxDescription = response[0]['tb_desc'];
      this.toolboxImage = response[0]['tb_image'];
      });
   }
   viewTool() {
    this.db.getToolsById({toolboxId: this.toolboxId}).subscribe((response) => {
      this.myTool = [];
      console.log(response);
      for(let i in response) {
      this.myTool.push({
        toolImage: response[i]['tool_image'],
        toolName: response[i]['tool_name'],
        toolDescription: response[i]['tool_desc'],
        toolId: response[i]['tool_id']
      });
      }
    });
   }
   editToolModal(myEvent, id) {
     console.log(id);
    let popover = this.popoverCtrl.create(EditToolPage, {
      toolId: id
    });
    popover.present({
      ev: myEvent
    });
    popover.onDidDismiss(() => {
      this.viewTool();
    });
   }
   scanModal(myEvent) {
    let popover = this.popoverCtrl.create(ScanModalPage, {
      toolboxId: this.toolboxId
    });
    popover.present({
      ev: myEvent
    });
   }
}



