import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { DBService } from '../../services/database.service';

/**
 * Generated class for the EditToolPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-tool',
  templateUrl: 'edit-tool.html',
})
export class EditToolPage {

  toolId;
  toolName;
  toolType;
  toolDescription;
  captureDataURL;

  constructor(public navCtrl: NavController, public navParams: NavParams, public db: DBService, public viewCtrl: ViewController) {
    this.db.getToolById({ 'toolId': navParams.get('toolId') }).subscribe((response) => {
      // console.log("toolbox"+response);
      this.toolName = response[0]['tool_name'];
      this.toolId = response[0]['tool_id'];
      this.toolType = response[0]['tool_type'];
      this.toolDescription = response[0]['tool_desc'];
      this.captureDataURL = response[0]['tool_image'];
  });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditToolPage');
  }

  capture(){
    // const cameraOptions: CameraOptions = {
    //   quality: 50,
    //   destinationType: this.camera.DestinationType.DATA_URL,
    //   encodingType: this.camera.EncodingType.JPEG,
    //   mediaType: this.camera.MediaType.PICTURE
    // }
  
    // this.camera.getPicture(cameraOptions)
    // .then((imageData) => {
    //   this.captureDataURL = 'data:image/jpeg;base64,' + imageData;
    //   console.log(this.captureDataURL);
    // })
    // .catch(err => {
    //   console.log(err);
    // });
    // this.captureDataURL = 'https://d2gg9evh47fn9z.cloudfront.net/800px_COLOURBOX6192516.jpg';
    // this.captureDataURL = 'data:image/jpeg;base64,';
    this.captureDataURL = 'https://img.freepik.com/free-vector/carpentry-tools-collecti_1096-325.jpg?size=338&ext=jpg';

  }

saveToDB(){
  // console.log(this.user);
  // if(this.owner == 'Team') {
  //   this.user = 'Team';
  // }
  this.db.editTool({
    'toolName': this.toolName,
    'toolId': this.toolId,
    'toolType': this.toolType,
    'toolDescription': this.toolDescription,
    'toolImage': this.captureDataURL
  })
  .subscribe((response) => {
    console.log(response);
  });
  console.log(this.toolType);
  this.close();
}

close() {
  this.viewCtrl.dismiss();
}

}



