import { Component } from '@angular/core';
import { NavController, NavParams,ViewController } from 'ionic-angular';
import { DBService } from '../../services/database.service';
/* import { Camera, CameraOptions } from '@ionic-native/camera'; */

@Component({
    selector: 'page-toolboxedit',
    templateUrl: 'toolbox-edit.html',
  })
  export class EditToolBox {
  // text:string;
    toolboxName;
    toolboxId;
    toolboxType;
    toolboxDescription;
    captureDataURL;
    owner;
    users = [];
    user;
  
    constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController, public db: DBService) {
      // this.text ='hello world';
      // console.log("ID"+navParams.get('toolboxId'))
      this.db.getToolBoxById({ 'toolboxId': navParams.get('toolboxId') }).subscribe((response) => {
        // console.log("toolbox"+response);
        this.toolboxName = response[0]['tb_name'];
        this.toolboxId = response[0]['tb_id'];
        this.toolboxType = response[0]['tb_type'];
        this.toolboxDescription = response[0]['tb_desc'];
        this.captureDataURL = response[0]['tb_image'];
        this.user = response[0]['tb_user'];
        // console.log(this.user);
        if(this.user == "Team") {
            this.owner = "Team";
        }
        else {
            this.owner = "Employee";
        }
    });
    // console.log(this.toolboxDescription);

      this.db.getUsers().subscribe((response) => {
        // console.log(response);
        for(let i in response) {
          this.users.push(response[i]['emp_id']);
        }
      });
    }

    capture(){
        // const cameraOptions: CameraOptions = {
        //   quality: 50,
        //   destinationType: this.camera.DestinationType.DATA_URL,
        //   encodingType: this.camera.EncodingType.JPEG,
        //   mediaType: this.camera.MediaType.PICTURE
        // }
      
        // this.camera.getPicture(cameraOptions)
        // .then((imageData) => {
        //   this.captureDataURL = 'data:image/jpeg;base64,' + imageData;
        //   console.log(this.captureDataURL);
        // })
        // .catch(err => {
        //   console.log(err);
        // });
        this.captureDataURL = 'https://images.homedepot-static.com/productImages/9a85624b-ca37-4c10-8444-261189871011/svn/black-husky-portable-tool-boxes-235580-64_1000.jpg';
      }
    
    saveToDB(){
      // console.log(this.user);
      if(this.owner == 'Team') {
        this.user = 'Team';
      }
      this.db.editToolBox({
        'toolboxName': this.toolboxName,
        'toolboxId': this.toolboxId,
        'toolboxType': this.toolboxType,
        'toolboxDescription': this.toolboxDescription,
        'toolboxImage': this.captureDataURL,
        'toolboxOwner': this.user
      })
      .subscribe((response) => {
        console.log(response);
      });
      this.close();
    }

    close() {
      this.viewCtrl.dismiss();
    }
    }


