import { Component } from '@angular/core';
import { NavController, NavParams, ModalController} from 'ionic-angular';
import { DBService } from '../../services/database.service';
//import { LoginDataProvider } from '../../providers/login-data/login-data';
//import { RestProvider } from '../../providers/rest/rest';

@Component({
  selector: 'page-toolbox-scan',
  templateUrl: 'toolbox-scan.html',
})

export class ToolboxScanPage {

    // toolboxurl = '\\toolbox\\toolbox';
    // public toolboxsdata;
    // result = {};
    // uresult = [];

    toolboxId;
    toolboxName;
    toolboxType;
    toolboxDescription;
    toolboxImage;
    myTool =[];
    rfid = ['To1'];
    status;
    scanStatus;
    count = 0;

    constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl : ModalController, public db: DBService) { 
        /*this.restProvider.getCall(this.toolboxurl,"")
          .then(data => {
              this.toolboxsdata = [{"name":"tool1","id":1001,"dec":"sss","size":"2X4X5","image":["path1","path2"],"type":"somewhat"},{"name":"tool2","id":1002,"dec":"sss","size":"2X4X5","image":["path1","path2"],"type":"somewhat"},{"name":"tool3","id":1003,"dec":"sss","size":"2X4X5","image":["path1","path2"],"type":"somewhat"},{"name":"tool4","id":1004,"dec":"sss","size":"2X4X5","image":["path1","path2"],"type":"somewhat"}];
              console.log("result", this.toolboxsdata);
              console.log("type", typeof(this.toolboxsdata));
            });*/

            this.toolboxId = navParams.get('toolboxId');
            this.viewToolBox();
            this.viewTool();
  }

  // doRefresh(refresher) {
  //   /*this.restProvider.getCall(this.toolboxurl,undefined)
  //   .then(data => {
  //       // this.toolboxdata = JSON.parse(data['_body']);
  //       // this.logindata.setairportData( this.toolboxdata);
  //       //   this.toolboxdata = this.logindata.getairportData();
  //       //   refresher.complete();
  //   });*/
  // }

  viewTool() {
    this.scanStatus = 'Scanning';
    this.db.getToolsById({toolboxId: this.toolboxId}).subscribe((response) => {
      this.myTool = [];
      console.log(response);
      for(let i in response) {
        if(this.rfid.includes(response[i]['tool_id'])) {
          this.status = 'found';
        }
        else {
          this.status = 'missing';
          this.count++;
        }
      this.myTool.push({
        toolImage: response[i]['tool_image'],
        toolName: response[i]['tool_name'],
        toolDescription: response[i]['tool_desc'],
        toolId: response[i]['tool_id'],
        status: this.status
      });
      }
    });
    this.scanStatus = 'Scan Completed';
   }
   viewToolBox() {
    this.db.getToolBoxById({'toolboxId':this.toolboxId}).subscribe((response) => {
      this.toolboxName = response[0]['tb_name'];
      this.toolboxId = response[0]['tb_id'];
      this.toolboxType = response[0]['tb_type'];
      this.toolboxDescription = response[0]['tb_desc'];
      this.toolboxImage = response[0]['tb_image'];
      });
   }

}


